@ease-in-out-circ: cubic-bezier(0.78, 0.14, 0.15, 0.86);
@drawer: drawer;
.@{drawer} {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  pointer-events: none;
  >* {
    transition: transform .3s @ease-in-out-circ, opacity .3s @ease-in-out-circ, box-shaow .3s @ease-in-out-circ;
  }
  &-mask {
    background: #000;
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
  }
  &-content-wrapper {
    position: absolute;
  }
  &-content {
    background: #fff;
    overflow: auto;
    z-index: 1;
    position: relative;
  }
  &-handle {
    position: absolute;
    top: 72px;
    width: 41px;
    height: 40px;
    cursor: pointer;
    pointer-events: auto;
    z-index: 0;
    text-align: center;
    line-height: 40px;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    &-icon {
      width: 14px;
      height: 2px;
      background: #333;
      position: relative;
      transition: background .3s @ease-in-out-circ;
      &:before,
      &:after {
        content: '';
        display: block;
        position: absolute;
        background: #333;
        width: 100%;
        height: 2px;
        transition: transform .3s @ease-in-out-circ;
      }
      &:before {
        top: -5px;
      }
      &:after {
        top: 5px;
      }
    }
  }
  &-left,
  &-right {
    .@{drawer}-content-wrapper,
    .@{drawer}-content {
      height: 100%;
    }
  }
  &-left {
    .@{drawer} {
      &-handle {
        right: -40px;
        box-shadow: 2px 0 8px rgba(0, 0, 0, .15);
        border-radius: 0 4px 4px 0;
      }
    }
    &.@{drawer}-open {
      .@{drawer} {
        &-wrapper {
          box-shadow: 2px 0 8px rgba(0, 0, 0, .15);
        }
      }
    }
  }
  &-right {
    .@{drawer} {
      &-content-wrapper {
        right: 0;
      }
      &-handle {
        left: -40px;
        box-shadow: -2px 0 8px rgba(0, 0, 0, .15);
        border-radius: 4px 0 0 4px;
      }
    }
    &.@{drawer}-open {
      & .@{drawer} {
        &-wrapper {
          box-shadow: -2px 0 8px rgba(0, 0, 0, .15);
        }
      }
    }
  }
  &-top,
  &-bottom {
    .@{drawer}-content-wrapper,
    .@{drawer}-content {
      width: 100%;
    }
    .@{drawer} {
      &-handle {
        left: 50%;
        margin-left: -20px;
      }
    }
  }
  &-top {
    .@{drawer} {
      &-handle {
        top: auto;
        bottom: -40px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, .15);
        border-radius: 0 0 4px 4px;
      }
    }
    &.@{drawer}-open {
      .@{drawer} {
        &-wrapper {
          box-shadow: 0 2px 8px rgba(0, 0, 0, .15);
        }
      }
    }
  }
  &-bottom {
    .@{drawer} {
      &-content-wrapper {
        bottom: 0;
      }
      &-handle {
        top: -40px;
        box-shadow: 0 -2px 8px rgba(0, 0, 0, .15);
        border-radius: 4px 4px 0 0;
      }
    }
    &.@{drawer}-open {
      .@{drawer} {
        &-wrapper {
          box-shadow: 0 -2px 8px rgba(0, 0, 0, .15);
        }
      }
    }
  }
  &.@{drawer}-open {
    > * {
        pointer-events: auto;
    }
    .@{drawer} {
      &-mask {
        opacity: .3;
      }
      &-handle {
        &-icon {
          background: transparent;
          &:before {
            transform: translateY(5px) rotate(45deg);
          }
          &:after {
            transform: translateY(-5px) rotate(-45deg);
          }
        }
      }
    }
  }
}


